.upload-progress {
    position: relative;
    width: 120px;
    height: 120px;
  }
  .upload-progress:after {
    content: "";
    position: absolute;
    background: #333333;
    left: 7%;
    right: 7%;
    bottom: 7%;
    top: 7%;
    z-index: 1;
    border-radius: 100%;
  }
  .upload-progress:before {
    content: "";
    position: absolute;
    background: #cdd1d3;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 0;
    border-radius: 100%;
  }
  
  .progress-val {
    position: absolute;
    color: #E64862;
    font-size: 60px;
    z-index: 2;
    width: 100%;
    line-height: 120px;
    top: 0;
    bottom: 0;
    left: 0;
    text-align: center;
  }
  
  .pie {
    position: absolute;
    top: 50%;
    margin-top: -60px;
    left: 50%;
    margin-left: -60px;
    width: 120px;
    height: 120px;
    overflow: hidden;
  }
  
  .first,
  .last {
    width: 60px;
    height: 120px;
    background: #E64862;
  }
  
  .first {
    border-top-left-radius: 60px;
    border-bottom-left-radius: 60px;
    -ms-transform-origin: 100% 50%;
    -webkit-transform-origin: 100% 50%;
    transform-origin: 100% 50%;
    position: absolute;
  }
  
  .last {
    border-top-right-radius: 60px;
    border-bottom-right-radius: 60px;
    -ms-transform-origin: 0% 50%;
    -webkit-transform-origin: 0% 50%;
    transform-origin: 0% 50%;
    margin-left: 60px;
    position: relative;
    z-index: 1;
    -ms-transform: rotate(180deg);
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
  }
  
  .per0 .pie {
    clip: rect(0, 120px, 120px, 60px);
  }
  
  .per0 .first {
    -ms-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  
